.select::after {
  content: "";
  display: inline-block;
  position: absolute;
  top: 45%;
  right: 12px;
  width: 8px;
  height: 8px;
  border-right: 2px solid #333;
  border-bottom: 2px solid #333;
  transform: translateY(-50%) rotate(45deg);
  pointer-events: none;
}

.select.focus::after {
  border-right: 2px solid #333;
  border-top: 2px solid #333;
  border-bottom: none;
  transform: translateY(-50%) rotate(-44deg);
  transition: all 0.2s ease-out;
}

.select__list {
  display: none;
  position: absolute;
  z-index: 2;
  left: 0;
  width: 100%;
  background: #fff;
  border-radius: 5px;
}
.select__list--active {
  display: block;
}
.select__list-item {
  padding: 10px 15px;
  cursor: pointer;
}
.select__list-item:hover {
  background: #84B245;
  color: #fff;
  transition: all 0.2s ease-out;
}

.checkbox {
  position: relative;
  width: 20px;
  height: 20px;
  background: #FFFFFF;
  border: 1px solid #E3E3E3;
  border-radius: 5px;
}
.checkbox-input {
  position: absolute;
  opacity: 0;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  margin: 0;
  z-index: 2;
}
.checkbox-label {
  display: flex;
  align-items: center;
  margin-top: 6px;
}
.checkbox__text {
  font-size: 12px;
  line-height: 21px;
  margin-left: 10px;
}

.checkbox-input:checked ~ .check {
  opacity: 1;
  background: url("../images/checkbox.png") no-repeat, #84B245;
  background-position: center;
  background-size: 54%;
  border: 1px solid #84B245;
  border-radius: 5px;
}

.check {
  display: block;
  position: absolute;
  width: 110%;
  height: 111%;
  left: -1px;
  top: -1px;
  opacity: 0;
}

